.home__title {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
}

.home__title-img {
  max-width: 68px;
  margin-left: 4px;
}

.home__tests {
  padding: 40px 0px;
  display: flex;
}

.home__articles {
  padding: 40px 0px;
  display: flex;
}

/* ==================== new test ==================== */

