}

.new-test__editor-info {
  background-color: #222222;
  border-radius: 15px;
  padding: 20px;
  max-width: 700px;
}

.new-test__editor-questions {
  margin-top: 25px;
  background-color: #222222;
  border-radius: 15px;
  padding: 20px;
